.main-container {
  display: flex;
  flex-direction: column;
}

body,
html {
  width: 100%;
  height: 100%;
  display: table;
  text-align: center;
}
body {
  background: #fde3a7;
}

.nameLabel {
  justify-content: flex-end;
}

/* Old paper background */
body {
  background: #112 url(../images/oldpaperbg.jpg) center
    no-repeat;
  background-size: cover;
  margin: 0;
}

.card-image {
  img.slide {
    width: 100%;
    height: auto;
    object-fit: cover;
    max-height: 100vh;
  }
}

div.row {
  align-items: flex-end;
  vertical-align: bottom;
}

div.col-2 {
  vertical-align:text-bottom;
  text-align: left;
}

h5 {
  
}

.file-upload {
  border: 1px solid black;
  border-radius: 3px;
  padding: 3px;
  
}

.file-upload input {
  overflow: hidden;
  width: 0;
}